.navImage{
    margin-top: 1rem;
    padding: 0rem;
    width: 100%;
    height: 4rem;
}
.intro{
  padding: 0.2rem;
  font-size: 0.3rem;
  border-bottom: 0.15rem solid #E9E9F2;
}
.address{
   position: relative;
   width: 100%;
   height: 1.5rem;
   line-height: 1.5rem;
   text-align: center;
   border-bottom: 0.15rem solid #E9E9F2;
   font-size: 0.35rem;
}
.addressname{
    position: absolute;
    top: 0.08rem;
    bottom: 0.08rem;
    left: 0.5rem;
    color: #B6B6B9;
    font-size: 0.4rem;
}
.mapbutton{
  position: absolute;
    top: 0.08rem;
    bottom: 0.08rem;
    right: 0.5rem;
    border: 0.01rem solid lightgray;
    padding: 0 0.3rem;
    margin: 0.25rem;
    line-height: 0.85rem;
    background: #F8F8F8;
    border-radius: 0.2rem;
}
.child-view{

 position: fixed;
 top: 0;
 left: 0;
 right: 0;
 bottom: 0;
 z-index:200;
 background-color: #fff;
 overflow-y: auto;
 -webkit-overflow-scrolling:touch;
}
.slide-enter-active, .slide-leave-active {
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
}
.slide-enter, .slide-leave-active {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}


.nav{
         position: absolute;
          top: 0rem;
          left: 0rem;
          right: 0rem;
          height: 1rem;
          background-color: rgba(0,0,0,1);
          color: rgba(255,255,255,1);
          line-height: 1rem;
          text-align: center;
          font-size: 0.4rem;
}
.back{
          position: absolute;
          top: 0.25rem;
          left: 0rem;
          bottom: 0.25rem;
          width: 0.5rem;
}


